html {
  font-family: 'Roboto', Helvetica, sans-serif;
  font-size: 100%;
  font-style: normal;
  font-weight: normal;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

body {
  color: #111;
  line-height: inherit;
}

h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0.8rem;
}

h5,
h6 {
  margin-top: 4rem;
  font-size: 1.25rem;
  line-height: 2rem;
}

h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 5rem;
  line-height: 8rem;
}

h2 {
  margin-top: 4rem;
  font-size: 2.5rem;
  line-height: 3rem;
}

h3 {
  margin-top: 3.2rem;
  font-size: 1.79688rem;
  line-height: 2rem;
}

h4 {
  margin-top: 2.375rem;
  font-size: 1.5rem;
  line-height: 2rem;
}

h1 + h2 {
  margin-top: 1.6rem;
}

h2 + h3,
h3 + h4,
h4 + h5 {
  margin-top: 0.8rem;
}

h5 + h6 {
  margin-top: -0.8rem;
}

small,
.text--small {
  font-size: 80%;
}

a {
  color: rgba(0, 0, 0, 0.9);
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  color: black;
}

a.active {
  color: black;
}

